@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Space+Mono&display=swap');

@font-face {
  font-family: "Machine";
  src: url("../fonts/B52.woff2") format("woff2"); }

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif; }

body {
  background-color: #EDEEF0; }

button {
  border: none;
  background: transparent; }

#root {
  max-width: 768px;
  margin: 0 auto; }

.card {
  margin: 0 12px;
  margin-top: 6px;
  padding: 12px;
  border-radius: 15px;
  box-shadow: 0px 2px 6px rgba(12, 12, 12, 0.1);
  background-color: #fff; }

.section-title {
  padding: 0 12px;
  margin-bottom: 6px;
  font-size: 17px;
  color: #818C99;
  text-transform: uppercase; }

.profiles {
  padding: 0 12px;
  display: flex;
  .items {
    position: relative;
    display: flex;
    flex-grow: 1;
    overflow-x: scroll;
    z-index: 1; }
  .shadow {
    position: relative;
    height: 40px;
    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 50px;
      background-image: linear-gradient(-90deg, #EDEEF0 30%, #818C9900 100%);
      z-index: 2; } }
  .item {
    padding: 6px 12px;
    margin-right: 8px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #6D7885;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    letter-spacing: -0.154px;
    text-transform: uppercase;
    border-radius: 10px;
    white-space: nowrap; }
  .edit {
    position: relative;
    margin-left: 8px;
    padding: 6px 4px;
    background-color: #fff;
    box-shadow: 1px 2px 2px rgba(12, 12, 12, 0.1);
    svg {
      display: block; } }
  .add {
    padding: 5px 12px;
    margin-right: 35px;
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='12' ry='12' stroke='black' stroke-width='2' stroke-dasharray='6' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
    border-radius: 12px;
    svg {
      display: block; } }
  svg {
    display: none; }
  .active {
    background-color: #fff;
    box-shadow: 1px 2px 2px rgba(12, 12, 12, 0.1);
    svg {
      margin-left: 8px;
      height: .7em;
      width: .7em;
      display: block; } }
  &-menu {
    position: absolute;
    display: none;
    top: 0;
    right: 0;
    flex-direction: column;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 1px 2px 32px rgba(12, 12, 12, 0.2);
    z-index: 3; }
  .open {
    display: flex; } }

header {
  padding: 0 12px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  background-color: #273036;
  // background-color: #35363A
  // background-color: #76baff
  .logo {
    position: relative;
    min-height: 20px;
    .letters {
      display: flex; }
    .letter {
      font-family: 'Space Mono', monospace; }
    .first-text, .second-text {
      position: absolute;
      top: 0;
      left: 0;
      font-weight: 200;
      font-size: 1em;
      font-family: inherit; }

    .first-text, .second-text {
      .text-wrapper {
        position: relative;
        display: inline-block;
        padding-top: 0.2em;
        padding-right: 0.05em;
        padding-bottom: 0.1em;
        overflow: hidden; } }

    .first-text, .second-text {
      .letter {
        display: block;
        transform-origin: 50% 100%;
        display: inline-block;
        line-height: 1em;
        transform: scale(0); } } }

  .calendar {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    .date {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      align-items: center;
      justify-content: center;
      .day {
        font-size: 30px;
        text-transform: uppercase; }
      .week {
        font-size: 16px;
        letter-spacing: .3em;
        text-transform: uppercase; } }
    &-actions {
      display: flex;
      justify-content: space-between;
      .prev, .next {
        width: 150px;
        height: 25px;
        position: relative;
        font-size: 12px;
        text-transform: uppercase; } }

    .long-arrow-right, .long-arrow-left {
      position: absolute;
      top: 0;
      display: block;
      width: 10px;
      height: 10px;
      border-top: 2px solid #000;
      border-left: 2px solid #000; }

    .long-arrow-right {
      right: 0;
      transform: rotate(135deg) translateX(6px) translateY(-2px); }
    .long-arrow-left {
      left: 0;
      transform: rotate(-45deg) translateX(-3px) translateY(6px); }

    .long-arrow-right, .long-arrow-left {
      &::after {
        content: "";
        display: block;
        width: 2px;
        height: 120px;
        background-color: black;
        transform: rotate(-45deg) translate(42px, 15px);
        left: 0;
        top: 0; } } } }

.rasp-slider {
  margin-top: 20px;
  .slick-slider {
    margin-top: 6px;
    .content {
      position: relative;
      margin: 10px;
      margin-top: 0;
      border-radius: 15px;
      box-shadow: 0px 2px 6px rgba(12, 12, 12, 0.1);
      background-color: #fff;
      overflow: hidden; }
    .title {
      position: relative;
      padding: 8px;
      text-align: center;
      text-transform: uppercase;
      background-color: #DCE1E6;
      // background-color: darken(#DCE1E6, 20%)
      // background-image: linear-gradient( 135deg, #5EFCE8 10%, #736EFE 100%)
      border-radius: 15px 15px 0 0;
      .date {
        font-size: 22px; }
      .week {
        font-size: 12px;
        letter-spacing: .1rem; }
      .pick-date {
        position: absolute;
        top: 9px;
        right: 8px;
        button {
          min-width: 0;
          padding: 8px;
          border-radius: 50%;
          background-color: transparent;
          box-shadow: none; } } }
    .slide-row {
      padding: 5px 0;
      height: 45px;
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid #e4e4e4;
      overflow: hidden;
      &:last-child {
        border-bottom: none; } }
    .main-info {
      margin-left: 10px;
      display: flex;
      align-items: center;
      .f-right {
        display: flex;
        flex-grow: 1;
        justify-content: flex-end; } }
    .second-info {
      margin: 0 10px;
      margin-top: 4px;
      display: flex;
      justify-content: space-between;
      .teacher {
        display: flex; }
      .shadow {
        position: relative;
        height: 16px;
        &::after {
          content: '';
          position: absolute;
          right: 0;
          top: 0;
          height: 100%;
          width: 50px;
          background-image: linear-gradient(-90deg, #fff 20%, #818C9900 100%);
          z-index: 2; } } }
    .badges {
      display: flex;
      .badge {
        color: #000;
        background-color: #DCE1E6;
        height: 20px;
        display: flex;
        padding: 0 12px;
        margin-top: 2px;
        margin-right: 5px;
        position: relative;
        font-size: 0.75rem;
        min-width: 20px;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        line-height: 1;
        border-radius: 10px;
        white-space: nowrap; }
      .arrow-right, .arrow-left {
        position: absolute;
        font-size: 22px;
        color: #5a5a5a;
        max-height: 20px; }
      .arrow-right {
        right: -4px; }
      .arrow-left {
        left: -4px; } }
    .time {
      display: flex;
      align-items: center;
      font-size: 14px;
      white-space: nowrap; }
    .audience {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      span {
        padding: 2px 10px;
        border-radius: 15px 0 0 15px;
        font-size: 12px;
        background-image: linear-gradient( 135deg, #81FBB8 10%, #28C76F 100%); } }
    .discipline {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden; }
    .teacher {
      white-space: nowrap; }
    .discipline, .teacher {
      font-size: 14px; } }

  .nextweek {
    margin: 6px 30px;
    padding: 6px;
    text-align: center;
    border-radius: 10px;
    background-color: #f48fb1; } }

.rasp-autobus {
  margin-top: 20px;
  .autobuses {
    margin: 0 12px;
    margin-top: 6px;
    padding: 12px;
    border-radius: 15px;
    box-shadow: 0px 2px 6px rgba(12, 12, 12, 0.1);
    background-color: #fff;
    .route {
      font-size: 20px;
      text-align: center; }
    .direction {
      margin: 8px 0;
      border-bottom: 1px solid #e3e3e3; }
    .title {
      margin-bottom: 8px;
      display: flex;
      align-items: center;
      font-size: 14px;
      svg {
        margin-right: 8px; } }
    .times {
      display: flex;
      margin-bottom: 8px;
      .c-1 {
        width: 100%; }
      .c-2 {
        width: calc( 100% / 2 ); }
      .c-3 {
        width: calc( 100% / 3 ); }

      .item {
        flex-grow: 1;
        padding: 4px 8px;
        margin-top: 2px;
        margin-right: 8px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 0.75rem;
        font-weight: 500;
        line-height: 1;
        border-radius: 6px;
        color: #000;
        background-color: #DCE1E6;
        &:last-child {
          margin-right: 0; }
        .time {
          font-size: 1.2rem; }
        .time-to {
          text-align: center; } } }
    .full-rasp {
      margin: 8px 0;
      font-size: 14px;
      color: #2a5885; }
    .add-direction {
      padding: 8px;
      margin-top: 2px;
      margin-right: 8px;
      display: flex;
      flex-grow: 1;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 1;
      border-radius: 6px;
      color: rgb(220, 0, 78);
      border: 1px solid rgba(220, 0, 78, 0.5);
      &:last-child {
        margin-right: 0; } }
    .actions {
      margin-top: 8px;
      display: flex;
      button {
        flex-grow: 1; } } } }

.motivation {
  margin-top: 20px;
  .bg {
    position: relative;
    margin: 0 12px;
    width: calc(100% - 24px);
    aspect-ratio: 1 / 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    box-shadow: 0px 2px 6px rgba(12, 12, 12, 0.1);
    &::before {
      content: "";
      position: absolute;
      height: 100%;
      width: 100%;
      border-radius: 12px;
      background-image: url("../images/123.jpg");
      background-size: cover;
      z-index: 1; } }
  .text {
    display: inline-block;
    max-width: 80%;
    font-size: 14px;
    line-height: 18px;
    color: rgba(0, 0, 0, 0.6);
    font-family: "Machine", sans-serif;
    z-index: 2; }
  .actions {
    position: absolute;
    left: 0;
    bottom: 8px;
    padding: 0 12px;
    width: calc( 100% - 24px );
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 2; }
  .like {
    display: flex;
    align-items: center;
    .count {
      font-size: 16px;
      margin-left: 4px; } }
  button {
    min-width: 0;
    padding: 8px;
    border-radius: 10px;
    background-color: transparent !important;
    box-shadow: none !important; } }

footer {
  margin-top: 20px;
  background-color: #273036;
  overflow: hidden;
  padding-bottom: 25px;
  svg {
    transform: translateY(-2px); }
  .title {
    padding: 12px;
    color: #fff;
    text-align: center;
    letter-spacing: .2rem; }
  .social {
    display: flex;
    justify-content: center;
    padding-bottom: 12px;
    a {
      margin: 0 8px;
      display: flex;
      align-items: center;
      color: #fff; } }
  .mail {
    text-align: center; }
  a {
    text-decoration: none;
    color: #fff; }
  svg {
    font-size: 1rem !important; } }

#select-rasp {
  display: flex;
  flex-direction: column;
  .row {
    display: flex;
    margin-top: 12px;
    justify-content: flex-end; }
  span#or {
    margin: 0 10px;
    padding: 6px 0;
    align-self: flex-end; }
  .MuiAutocomplete-root {
    margin-top: 6px; }
  .MuiAutocomplete-inputRoot {
    padding-right: 0; }
  .MuiAutocomplete-clearIndicator {
    display: none; } }

.helt-modal {
  .direction {
    padding: 20px 15px;
    margin-bottom: 5px;
    font-size: 14px;
    background-color: #b6cdff;
    border-radius: 15px 15px 0 0;
    span {
      font-size: 1.2rem; } }
  .content {
    overflow: scroll;
    height: calc( 100% - 90px ); }
  .hours {
    padding: 4px 15px;
    display: flex;
    align-items: center;
    font-size: 20px;
    border-bottom: 1px solid #eee;
    .title {
      margin-right: 20px; }
    .items {
      display: flex;
      align-items: center;
      .item {
        padding: 4px;
        font-size: 14px; }
      .item.active {
        flex-grow: 1;
        padding: 4px 8px;
        margin: 0 8px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 0.75rem;
        font-weight: 500;
        line-height: 1;
        border-radius: 6px;
        color: #000;
        background-color: #DCE1E6;
        &:last-child {
          margin-right: 0; }
        .time {
          font-size: 1rem; }
        .time-to {
          text-align: center; } } } }
  .hours.active {
    background-color: #e3e2e3; } }

.modal {
  .dir-name {
    padding: 20px 15px;
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 14px;
    background-color: #b6cdff;
    border-radius: 15px 15px 0 0;
    span {
      font-size: 1.2rem; } }
  .row {
    padding: 8px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #eee; }
  .mx-w {
    max-width: 80%;
    overflow: hidden;
    white-space: nowrap; }
  svg {
    font-size: 28px; }
  .content {
    overflow: scroll;
    height: calc( 100% - 80px ); } }

.MuiAlert-root {
  margin: 12px;
  background-color: #b6cdff !important;
  border-radius: 10px !important;
  color: #000 !important; }

.MuiDialogContent-root:first-child {
  padding: 0;
  padding-top: 0 !important; }

.MuiDialog-paper {
  margin: 0 !important;
  width: calc( 100% - 64px );
  .MuiDialogActions-root {
    button {
      &:first-child {
        color: #333; }
      &:last-child {
        font-weight: 600;
        color: #3f51b5; } } } }

.react-datepicker {
  width: 100%;
  border: none;
  overflow: hidden;
  user-select: none;

  &__custom-header {
    margin: 10px 0;
    display: flex;
    .month {
      display: flex;
      flex-grow: 1;
      align-items: center;
      justify-content: center;
      font-size: 1rem; }
    button {
      padding: 15px !important; } }
  &__header {
    padding: 0px;
    border: none;
    background-color: #fff;
    border-top-right-radius: 0 !important; }
  &__month-container {
    width: 100%; }
  &__week {
    display: flex; }
  &__week {
    .react-datepicker__day {
      &:first-child {
        border-radius: 50% 0 0 50% !important; }
      &:last-child {
        border-radius: 0 50% 50% 0 !important; } } }
  &__day {
    width: calc( 100% / 7 - 7px);
    aspect-ratio: 1 / 1;
    margin: 0;
    padding: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0 !important;
    -webkit-tap-highlight-color: transparent;
    .btn-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      aspect-ratio: 1 / 1;
      color: #000 !important;
      font-size: 1rem !important; } }
  &__day--in-range {
    background-color: #DCE1E6 !important; }
  &__day--keyboard-selected {
    background-color: #ffffff00; }
  &__day--outside-month {
    .btn-wrapper {
      color: lighten(#000, 70%) !important; } }
  .active {
    background-color: darken(#DCE1E6, 20%);
    border-radius: 50%; }
  &__day-names {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0; }
  &__day-name {
    width: calc( 100% / 7 - 7px );
    color: #00000061;
    text-align: center;
    line-height: 1;
    margin: 3px; }

  .MuiIconButton-root {
    padding: 0; } }
